            <!-- 场所详情 -->
            <div class="add-site add-site-detail" v-if="type === 'site'">
                <Row>
                    <Col span="3">
                        <div class="name">场所:</div>
                    </Col>
                    <Col span="4">
                        国家地区：中国
                    </Col>
                    <Col span="4">
                        场所名称：重庆
                    </Col>
                </Row>
                <Row class="parameter">
                    <Col span="3">
                        <div class="name">选择参数:</div>
                    </Col>
                    <Col span="4">
                        运输类型：铁运
                    </Col>
                    <Col span="4">
                        集装箱尺寸：20
                    </Col>
                    <Col span="4">
                        集装箱类型：普柜
                    </Col>
                </Row>
            </div>
            <!-- 综保区详情 -->
            <div class="add-site add-site-detail" v-if="type === 'cpa'">
                <Row>
                    <Col span="4">
                        国家地区：中国
                    </Col>
                    <Col span="10">
                        综保区名称：重庆两江综保区
                    </Col>
                </Row>
            </div>
            <!-- 运输详情 -->
            <div class="add-site add-site-detail" v-if="type === 'transport'">
                <Row>
                    <Col span="3">
                        <div class="name">场所:</div>
                    </Col>
                    <Col span="4">
                        启运地：中国
                    </Col>
                    <Col span="4">
                        目的地：重庆
                    </Col>
                </Row>
                <Row class="parameter">
                    <Col span="3">
                        <div class="name">选择参数:</div>
                    </Col>
                    <Col span="4">
                        运输类型：铁运
                    </Col>
                    <Col span="4">
                        集装箱尺寸：20
                    </Col>
                    <Col span="4">
                        集装箱类型：普柜
                    </Col>
                </Row>
            </div>
        </div>
        <div class="white-bg middle-white-bg">
            <div class="product-list">
                <div class="product-content only-one">
