                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="white-bg">
            <Row class="set-dead-line">
                <Col span="10">
                    <Icon type="md-alert" />方案报价截止时间：2018-10-09
                </Col>
