</template>

<script>
    import AdtTaskContent from '@/components/adt-task-content/Index'
    export default {
        components: {
            AdtTaskContent
        },
        props: ['type'],
        data () {
            return {
                isModalShow: false,
                isSceneModalShow: false,
                sceneTabIndex: 1,
                tabIndex: 1,
                sceneTitleNames: ['场景1', '场景2'],
                titleNames: ['设置基础任务费用时长', '设置附加任务费用时长'],
                basicTask: ['fdfd', '发个施工队'],
                gqShow: true,
                isBasicTask: true
            }
        },
        methods: {
            handleSceneBtn (index) {
                console.log(index)
            },
            handleBtn (index) {
                this.tabIndex = index + 1
                // 基础任务
                if (index === 0) {
                    this.isBasicTask = true
                } else {
                    // 附加任务
                    this.isBasicTask = false
                }
            },
            edit () {
                this.$router.push(`/product-editor/edit-transport-product?type=${this.type}`)
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import './style.scss';
</style>
